* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', "Arial", sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', "Arial", sans-serif;
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  position: fixed;
  z-index: 1;
  width: 100%;
  max-height: 100%;
  padding: 0 20px;
  box-shadow: 0px 6px 10px #cccccc;
  background-color: #ffffff;
}
@media (max-width: 900px) {
  .header {
    grid-template-columns: 1fr;
    overflow-x: auto;
  }
}
.header__wrap {
  width: 100%;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: 1fr;
  background-color: #ffffff;
}
@media (max-width: 900px) {
  .header__wrap {
    grid-template-columns: 1fr;
  }
}
.page-title {
  margin: 15px 0;
  font-size: 28px;
  font-family: 'Akshar', sans-serif;
  font-weight: bold;
  color: #000000;
  white-space: nowrap;
}
@media (max-width: 900px) {
  .page-title {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1;
    margin: 0;
    font-size: 32px;
  }
}
.nav {
  display: flex;
  align-items: center;
  justify-content: end;
}
@media (max-width: 900px) {
  .nav {
    flex-direction: column;
    align-items: end;
    justify-content: start;
    gap: 20px;
  }
}
.nav__button {
  display: none;
  border: none;
  background-color: transparent;
}
@media (max-width: 900px) {
  .nav__button {
    display: block;
    width: 80px;
    height: 80px;
  }
}
.nav__list {
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 20px;
}
@media (max-width: 900px) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  display: flex;
  flex-direction: column;
  align-items: end;
  height: min-content;
  padding-bottom: 20px;
}
.nav__item {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__link {
  height: 100%;
  font-size: 19px;
  white-space: nowrap;
  position: relative;
  padding: 15px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto', "Arial", sans-serif;
  color: #000000;
  text-decoration: none;
  opacity: 0.8;
}
@media (max-width: 900px) {
  .nav__link {
    font-size: 22px;
  }
}
.nav__link--active {
  opacity: 1;
  pointer-events: none;
}
.nav__link--active::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #cccccc;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 1;
}
.nav__link:hover::before,
.nav__link:focus::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #cccccc;
  animation-duration: 1s;
  animation-name: linkanimate;
}
@keyframes linkanimate {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
.nav__shopping-cart {
  width: 35px;
  height: 35px;
}
.btn-icon {
  height: 75px;
  width: 75px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.btn-icon--active {
  transform: rotate(45deg);
}
.btn-icon__line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000000;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.btn-icon__line--top {
  stroke-dasharray: 40 139;
}
.btn-icon__line--bottom {
  stroke-dasharray: 40 180;
}
.btn-icon--active .btn-icon__line--top {
  stroke-dashoffset: -98px;
}
.btn-icon--active .btn-icon__line--bottom {
  stroke-dashoffset: -138px;
}
.main--index {
  flex-grow: 1;
  padding: 100px 20px;
}
.main--catalog {
  padding: 100px 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 30px;
}
.footer {
  padding: 20px;
  box-shadow: 0px -6px 10px #cccccc;
}
.footer__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 600px) {
  .footer__wrap {
    flex-direction: column;
    gap: 30px;
  }
}
.social-list {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 20px;
  list-style: none;
}
@media (max-width: 600px) {
  .social-list {
    flex-direction: column;
  }
}
.social-list__link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 0;
  text-decoration: none;
  color: #000000;
  font-family: 'Roboto', "Arial", sans-serif;
  opacity: 0.8;
  font-size: 17px;
}
.social-list__link:hover,
.social-list__link:hover .social-list__icon,
.social-list__link:focus,
.social-list__link:focus .social-list__icon {
  opacity: 1;
}
.social-list__icon {
  opacity: 0.5;
}
.rights {
  font-family: 'Roboto', "Arial", sans-serif;
  font-size: 15px;
  opacity: 0.7;
}
.banner {
  width: 100%;
  height: 300px;
  position: relative;
}
.banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.banner__info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.6);
}
.banner__title {
  font-size: 22px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 500;
}
.banner__text {
  font-size: 18px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 300;
}
.offer {
  width: 100%;
  height: 300px;
}
.offer__title {
  font-size: 28px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  text-transform: capitalize;
}
.catalog {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 20px;
}
.catalog__title {
  font-size: 28px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  text-transform: capitalize;
}
.catalog__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}
.catalog__item {
  width: 280px;
  background: #fff;
  box-shadow: 20px 20px 40px #cecece, -20px -20px 40px #f2f2f2;
}
.product-card {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, min-content) 1fr;
  grid-row-gap: 10px;
}
.product-card__title {
  order: 2;
  font-size: 20px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 500;
  padding: 0 6px;
}
.product-card__description {
  order: 3;
  font-size: 17px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 300;
  color: #000000;
  padding: 0 6px;
}
.product-card__img-link {
  width: 280px;
  height: 280px;
  overflow: hidden;
}
.product-card__img {
  order: 1;
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: flex;
  transition: 0.3s ease-in-out;
}
.product-card__img:hover,
.product-card__img:focus {
  transform: scale(1.3);
  transition: 0.3s ease-in-out;
}
.product-card__controls {
  padding: 0 6px 15px 6px;
  order: 4;
  display: flex;
  justify-content: space-between;
  align-items: end;
}
.product-card__show-info {
  font-size: 17px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 400;
  text-decoration: underline;
  color: #bebebe;
  transition: all ease-in 0.2s;
  cursor: pointer;
}
.product-card__show-info:hover,
.product-card__show-info:focus {
  color: #000000;
}
.product-card__to-cart {
  cursor: pointer;
  background-color: transparent;
  border-radius: 8px;
  text-transform: uppercase;
  border: none;
  transition: all ease-in 0.2s;
}
.product-card__to-cart:hover .product-card__cart-icon > path,
.product-card__to-cart:focus .product-card__cart-icon > path {
  transition: all ease-in 0.2s;
  fill: #000000;
}
.product-card__cart-icon > path {
  transition: all ease-in 0.2s;
  fill: #bebebe;
}
