.header {
	position: fixed;
	z-index: 1;
	width: 100%;
	max-height: 100%;
	padding: 0 @standart-side-padding;
	box-shadow:  0px 6px 10px @header-footer-box-shadow-color;
	background-color: @standart-white;

	@media (max-width: @tablet-width) {
		grid-template-columns: 1fr;
		overflow-x: auto;
	}
}

.header__wrap {
	width: 100%;
	display: grid;
	grid-template-columns: min-content 1fr;
	grid-template-rows: 1fr;
	background-color: @standart-white;

	@media (max-width: @tablet-width) {
		grid-template-columns: 1fr;
	}
}

.page-title {
	margin: @header-top-padding 0;
	font-size: 28px;
	font-family: 'Akshar', sans-serif;
	font-weight: bold;
	color: @standart-black;
	white-space: nowrap;

	@media (max-width: @tablet-width) {
		position: absolute;
		top: 20px;
		left: @standart-side-padding;
		z-index: 1;
		margin: 0;
		font-size: 32px;
	}
}

.nav {
	display: flex;
	align-items: center;
	justify-content: end;

	@media (max-width: @tablet-width) {
		flex-direction: column;
		align-items: end;
		justify-content: start;
		gap: 20px;
	}
}

.nav__button {
	display: none;
	border: none;
	background-color: transparent;

	@media (max-width: @tablet-width) {
		display: block;
		width: 80px;
		height: 80px;
	}
}

.nav__list {
	height: 100%;
	list-style: none;
	display: flex;
	align-items: center;
	justify-content: end;
	gap: 20px;

	@media (max-width: @tablet-width) {
		display: none;
	}

	&--open {
		display: flex;
		flex-direction: column;
		align-items: end;
		height: min-content;
		padding-bottom: 20px;
	}
}

.nav__item {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.nav__link {
	height: 100%;
	font-size: @nav-link-text-size;
	white-space: nowrap;
	position: relative;
	padding: @header-top-padding 10px;
	display: flex;
	justify-content: center;
	align-items: center;

	font-family: 'Roboto', "Arial", sans-serif;
	color: @standart-black;
	text-decoration: none;
	opacity: 0.8;

	@media (max-width: @tablet-width) {
		font-size: @nav-link-mobile-text-size;
	}

	&--active {
		opacity: 1;
		pointer-events: none;
		&::before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: @header-footer-box-shadow-color;
		}
	}

	&:hover,
	&:focus {
		opacity: 1;

		&::before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: #cccccc;

			animation-duration: 1s;
			animation-name: linkanimate;
		}
	}
}

@keyframes linkanimate {
	from {
		width: 0%;
	}
	to {
		width: 100%;
	}
}

.nav__shopping-cart {
	width: 35px;
	height: 35px;
}

// navigation button animation styles
.btn-icon {
	height: 75px;
	width: 75px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;

	&--active {
		transform: rotate(45deg);
	}
}
.btn-icon__line {
  fill:none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: @standart-black;
  stroke-width:5.5;
  stroke-linecap:round;
}
.btn-icon__line--top {
  stroke-dasharray: 40 139;
}
.btn-icon__line--bottom {
  stroke-dasharray: 40 180;
}
.btn-icon--active .btn-icon__line--top {
  stroke-dashoffset: -98px;
}
.btn-icon--active .btn-icon__line--bottom {
  stroke-dashoffset: -138px;
}
